#Home {
  color: #fff;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  position: relative;
}

#Home::after {
  content: "";
  background: #43405d
    url("https://res.cloudinary.com/dafolrlpj/image/upload/v1622633669/gallery/chlicgzxq35ank8z0806.png")
    no-repeat center center/cover;
  background-attachment: fixed;
  opacity: 0.8;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  filter: grayscale(100%);
  transition-property: filter, opacity;
  transition-duration: 0.33s;
  transition-timing-function: ease;
  z-index: -3;
}

#Home:hover::after {
  filter: grayscale(0%);
  transition-property: filter, opacity;
  transition-duration: 0.33s;
  transition-timing-function: ease;
}

.hero {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  top: 30px;
  height: 260px;
  width: 260px;
  position: relative;
  transform: scale(1.5);
}

.text-area {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.circle {
  background-color: #222831;
  height: 100%;
  width: 100%;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.gradient-circle {
  background: conic-gradient(
    #706897 0%,
    #43405d 40%,
    #fff 40%,
    #fff 60%,
    #583d72 60%,
    #4a266a 100%
  );
  height: 280px;
  width: 280px;
  z-index: -2;
  border-radius: 50%;
  position: absolute;
  top: -10px;
  left: -10px;
}

.pointer {
  background-color: orange;
  border: solid 2px #43405d;
  border-radius: 50%;
  height: 20px;
  width: 20px;
  display: block;
}

.pointer-container {
  position: absolute;
  top: -30px;
  left: 120px;
  width: 20px;
  height: 160px;
  animation: rotate 6s linear forwards infinite;
  transform-origin: bottom center;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.hero.grow {
  animation: grow 3s linear forwards;
}

@keyframes grow {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(1.5);
  }
}

.hero.shrink {
  animation: shrink 3s linear forwards;
}

@keyframes shrink {
  from {
    transform: scale(1.5);
  }

  to {
    transform: scale(1);
  }
}
